import React from "react";
import { Connection, clusterApiUrl, PublicKey } from "@solana/web3.js";
import { WalletAdapterNetwork } from "@solana/wallet-adapter-base";
import {
  ConnectionProvider,
  WalletProvider
} from "@solana/wallet-adapter-react";
import {
  WalletModalProvider,
  WalletMultiButton
} from "@solana/wallet-adapter-react-ui";

// Import only the wallets you need (Phantom, Solflare) directly:
import { PhantomWalletAdapter } from "@solana/wallet-adapter-phantom";
import { SolflareWalletAdapter } from "@solana/wallet-adapter-solflare";

import { GatewayProvider, useGateway, GatewayStatus } from "@civic/solana-gateway-react";

// (Optional) default styling for the Solana wallet modal
import "@solana/wallet-adapter-react-ui/styles.css";

// 1) A small component that uses the `useGateway` hook:
const MyGatewayChecker: React.FC = () => {
  const { gatewayStatus, gatewayToken, requestGatewayToken } = useGateway();

  const handleRequestPass = async () => {
    try {
      await requestGatewayToken();
    } catch (err) {
      console.error("Error requesting pass:", err);
    }
  };

  return (
    <div style={{ border: "1px solid #ccc", margin: "1rem 0", padding: "1rem" }}>
      <h2>My Gateway Status</h2>
      <p>Status: <b>{GatewayStatus[gatewayStatus]}</b></p>
      {gatewayToken ? (
        <p>
          Found gateway token: <br />
          <b>{gatewayToken.publicKey.toBase58()}</b>
        </p>
      ) : (
        <p>No gateway token found yet.</p>
      )}
      <button onClick={handleRequestPass}>Request or Refresh Pass</button>
    </div>
  );
};

function App() {
  // 2) Connect to devnet
  const network = WalletAdapterNetwork.Devnet;
  const endpoint = clusterApiUrl(network);

  // 3) Set up **only** the wallet adapters we actually need
  const wallets = [
    new PhantomWalletAdapter(),
    new SolflareWalletAdapter({ network })
  ];

  // 4) Hardcode the gatekeeper network
  const gatekeeperNetwork = new PublicKey("uniqobk8oGh4XBLMqM68K8M2zNu3CdYX7q5go7whQiv");

  return (
    <ConnectionProvider endpoint={endpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>
          <GatewayProvider
            connection={new Connection(endpoint, "processed")}
            cluster="devnet"
            gatekeeperNetwork={gatekeeperNetwork}
            wallet={{
              publicKey: wallets[0].publicKey,
              signTransaction: wallets[0].signTransaction?.bind(wallets[0])!,
              connected: true,
            }}
          >
            <div style={{ margin: "2rem" }}>
              <h1>Civic Gateway Without IdentityButton</h1>

              {/* 5) Provide a way for user to connect wallet */}
              <WalletMultiButton />

              {/* 6) Our custom gateway checker */}
              <MyGatewayChecker />
            </div>
          </GatewayProvider>
        </WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
}

export default App;
